import React, { Component } from 'react';
import styled from 'styled-components';

const Gif = styled.div`
    text-align: center;
    width: 100vw;
    height: 100vw; 
    
    position: relative;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
      
    margin: auto;

    .gif {
        width: 50vw;
    }

    .gifContainer {
        font-size: 1.5vw;
    }

    .returnLink {
        font-size: 1.2em;
    }
`;

export interface listOfMovies {
    movie: object
}

class Gifs extends Component<{}, { listOfMovies: any[], listOfUrls: String[] }> {
    constructor(props: any) {
        super(props);

        this.state = {
            listOfMovies: [
                {
                    comments: String,
                    imdb: String,
                    mtc: String,
                    rtc: String,
                    smn: String,
                    title: String,
                    pictureUrl: String
                }],
            listOfUrls: []
        }
    }


    componentDidMount() {
        this.callServerForDatabase()
            .then(data => this.setState({ listOfMovies: data }))
            .catch(err => console.log(err));
    };

    callServerForDatabase = async () => {
        const response = await fetch('/database');
        const body = await response.json();

        console.log("Response Body: ", body)

        if (response.status !== 200) throw Error(body.message);
        return body;
    };

    createListOfUrlsAndTitles() {

        let urlArr: {}[] = [];

        this.state.listOfMovies.map((movie: any) => {
            
            for (let i = 0; i < movie.pictureUrl.length; i++) {
                if (movie.pictureUrl) {
                    urlArr.push({ 
                        title: movie.title, 
                        pictureUrl: movie.pictureUrl[i] 
                    })
                }
            }
        })

        return urlArr;
    };

    listOfMovies() {
        const listOfGifs = this.createListOfUrlsAndTitles();

            console.log("LOM: ", this.state.listOfMovies)
            console.log("LOU: ", this.state.listOfUrls)

            return (
                listOfGifs.map((gif: any, index: any) => {
                    return (
                        <div className="gifContainer" key={index}>
                            <p>
                                <img src={gif.pictureUrl} alt="This gif is actually hilarious" className="gif" />
                                <br />
                                {gif.title}
                            </p>
                        </div>
                    )
                })
            )
        }

    render() {
        return(
            <Gif>
            { this.listOfMovies() }

            < br />
            <a className="returnLink" href="/"><i>Return To Main Page</i></a>
            </Gif >
        )}
};

export default Gifs;