import React, { Component } from 'react';
import styled from 'styled-components';
import axios from 'axios';

const Load = styled.div`
    text-align: center;
    width: 60vw;
    height: 25vw;
    
    position: relative;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
      
    margin: auto;

    font-size: calc(1em + 1vw);

    .titleSelect {
        height: auto;
        font-size: 0.5em;
    }

    .uploadTitle {
        margin-top: 10px;
        margin-bottom: -10px;
    }

    .returnLink {
        font-size: 1.2em;
    }
`;

interface HTMLInputEvent extends Event {
    target: HTMLInputElement & EventTarget & HTMLFormElement;
}

export interface listOfMovies {
    movie: object
}

class Upload extends Component<{}, { uploadedFile: any, loaded: number, listOfMovies: any }> {
    constructor(props: any) {
        super(props);

        this.state = {
            uploadedFile: null,
            loaded: 0,
            listOfMovies: [
                {
                    comments: String,
                    imdb: String,
                    mtc: String,
                    rtc: String,
                    smn: String,
                    title: String,
                    __v: Number,
                    _id: String
                }]
        }
    }

    componentDidMount() {
        this.callServerForDatabase()
            .then(data => this.setState({ listOfMovies: data }))
            .catch(err => console.log(err));
    };

    callServerForDatabase = async () => {
        const response = await fetch('/database');
        const body = await response.json();

        if (response.status !== 200) throw Error(body.message);
        return body;
    };

    handleUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {

        // @ts-ignore: Object is possibly 'null'.
        console.log(e.target.files[0])

        this.setState({
            // @ts-ignore: Object is possibly 'null'.
            uploadedFile: e.target.files[0],
            loaded: 0
        });

        e.preventDefault();
    };

    onClickHandler = () => {
        const data = new FormData()
        data.append('file', this.state.uploadedFile)
        axios.post("http:localhost:3000/upload", data, {

        })
            .then(res => {
                console.log(res.statusText)
            })
    }

    render() {
        return (
            <Load>
                <div className="uploadTitle">Upload a funny Gif for one of the Movies we've seen!</div>
                <br />
                <form method="POST" action="/upload" encType="multipart/form-data">
                    <label className="titleSelect">For Which Movie?  </label>
                    <select className="titleSelect" name="title">
                        {this.state.listOfMovies.map((movie: any) =>
                            <option value={movie.title}>{movie.title}</option>
                        )}
                    </select>
                    <br />
                    <br />
                    <input type="file" name="funnygif" onChange={this.handleUpload} />
                    <button type="submit" onClick={this.onClickHandler}>Upload GIF</button>
                </form>
                <br />
                <a className="returnLink" href="/"><i>Return To Main Page</i></a>
            </Load>
        )
    }
};

export default Upload;